.loader {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    position: absolute;
    width: 100vw;
    background-color: #fff;
    z-index: 1000;
  }
  
  .spinner {
    width: 100px;
    height:100px;
    border: 2px solid #f3f3f3;
    border-top: 4px solid #2d3134;
    border-radius: 50%;
    animation: spin 1s linear infinite;
  }
  
  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
  