.navbar{
    display: flex;
    justify-content: space-between;
    background-color: rgb(225, 132, 163);
    padding: 5px 10px;
    align-items:center ;
    .logo{
        span{
            font-size: 1.6rem;
            cursor: pointer;
        }
    }
    .search-bar{
        input{
            border-radius: 10px;
            background-color: rgb(237, 231, 231);
            border: none;
            outline: none;
            padding: 10px;
            height: 40px;
            width: 300px;
        }

    }
    .log-out{
        button{

            padding: 10px 20px;
            letter-spacing: 1.5px;
            border-radius: 15px;
            background-color: #000;
            color: #fff;
            cursor: pointer;
        }
    }
}