.todo-item{
    background-color: rgb(227, 227, 227);
    box-shadow: 2px 2px 2px rgb(123, 123, 123);
    display:flex ;
    align-items: center;
    justify-content: space-between;
    width: fit-content;
    margin-left: auto;
    margin-right: auto;
    border-radius: 10px;
    margin-bottom: 10px;
    margin-top: 20px;
    gap: 10px;
    padding: 10px;
    width: 600px;
    .task{
        // background-color: red;
        max-width: 400px;
        cursor: pointer;
        padding: 5px ;
    }
    button{
        padding: 10px 20px;
        letter-spacing: 1.5px;
        border-radius: 15px;
        background-color: #000;
        color: #fff;
        cursor: pointer;
    }
}