.new-todo{
    display: flex;
    justify-content: center;
    gap: 10px;
    padding: 20px 0px;
    input{
        width: 400px;
        height: 40px;
        border: none;
        outline: none;
        border-radius: 10px;
        padding: 10px;
        background-color: rgb(240, 234, 234);
    }
    button{
        margin-left: 10px;
        text-transform: uppercase;
        cursor: pointer;
        border-radius:15px;
        border: none;
        outline: none;
        background-color: #000;
        color: #fff;
        padding: 10px 20px;
    }
}